import React from 'react';

const HeroSection = () => {
  return (
    <div className="absolute right-0 top-24 rounded-tl-lg rounded-bl-lg w-[700px] h-[180px] bg-[#00564999] flex items-center justify-center gap-6">
      <h1 className="text-white text-[42px] font-bold">Sophrologie</h1>
      <div className="border border-[#fff] h-8 w-[1px]" />
      <blockquote className="text-white text-lg italic max-w-xs">
        <p className="mb-2">"J'ai décidé d'être heureux. C'est bon pour la santé !"</p>
        <cite className="text-sm font-light opacity-90">— Voltaire</cite>
      </blockquote>
    </div>
  );
};

export default HeroSection;